import Vue from 'vue'
import Router from 'vue-router'

import Login from './components/Login.vue' // 
import Home from './components/Home.vue' // 
import Welcome from "./components/Welcome.vue" ///

//   VideoPreview/
//   VideoPreview/videoPreview.vue
import VidP_videoPreview from "./components/VideoPreview/videopreview.vue"

//   ManManage/
// -  ManManage/manmessage.vue
import MaM_manmessage from "./components/ManManage/manmessage.vue"
// -  ManManage/company.vue
import MaM_company from "./components/ManManage/company.vue"
// -  ManManage/roles.vue
import MaM_roles from "./components/ManManage/roles.vue"
// -  ManManage/authorized.vue
import MaM_authorized from "./components/ManManage/authorized.vue"

//   EqManage/
// -  EqManage/eqmessage.vue
import EqM_eqmessage from "./components/EqManage/eqmessage.vue"

//  EqLayout/
// -  EqLayout/eqlayout.vue
import EqL_eqlayout from "./components/EqLayout/eqlayout.vue"

//   DataManage/
// -  DataManage/record.vue
import DaM_record from "./components/DataManage/record.vue"
// -  DataManage/log.vue
import DaM_log from "./components/DataManage/log.vue"

//   DataFrom/
// -  DataFrom/record.vue
import DaF_datafrom from "./components/DataFrom/datafrom.vue"

// 
import Sys_sysconfig from "./components/SysConfig/sysconfig.vue"

Vue.use(Router)

/**
 * push
 */
const routerPush = Router.prototype.push
Router.prototype.push = function push(location) {
    return routerPush.call(this, location).catch(error => error)
}
const router = new Router({
    mode: 'history',
    routes: [
        { path: '/', redirect: '/login' },
        {
            path: '/login',
            component: Login,
            name: 'Login',
            meta: [
                { name: '' }
            ]
        },

        {
            path: '/home',
            component: Home,
            name: 'Home',
            meta: [
                { name: '' }
            ],
            redirect: "/eqmeqmessage",
            children: [{
                path: "/welcome",
                component: Welcome,
                name: 'Welcome',
                meta: [
                    { name: '' }
                ]
            }, {
                path: "/vidpvideopreview",
                component: VidP_videoPreview,
                name: 'VidP_videoPreview',
                meta: [
                    { name: '' }, //{ name: '', url: '/welcome' },
                    { name: '' }
                ]
            }, {
                path: "/mammanmessage",
                component: MaM_manmessage,
                name: 'MaM_manmessage',
                meta: [
                    { name: '' }, //{ name: '', url: '/welcome' },
                    { name: '' },
                    { name: '' }
                ]
            }, {
                path: "/authorized",
                component: MaM_authorized,
                name: 'MaM_authorized',
                meta: [
                    { name: '' }, //{ name: '', url: '/welcome' },
                    { name: '' },
                    { name: '', url: '/mammanmessage' },
                    { name: '' }
                ]
            }, {
                path: "/mamcompany",
                component: MaM_company,
                name: 'MaM_company',
                meta: [
                    { name: '' }, //{ name: '', url: '/welcome' },
                    { name: '' },
                    { name: '' }
                ]
            }, {
                path: "/mamroles",
                component: MaM_roles,
                name: 'MaM_roles',
                meta: [
                    { name: '' }, //{ name: '', url: '/welcome' },
                    { name: '' },
                    { name: '' }
                ]
            }, {
                path: "/eqmeqmessage",
                component: EqM_eqmessage,
                name: 'EqM_eqmessage',
                meta: [
                    { name: '' },
                    { name: '' }
                ]
            }, {
                path: "/eqleqlayout",
                component: EqL_eqlayout,
                name: 'EqL_eqlayout',
                meta: [
                    { name: '' },
                    { name: '' }
                ]
            }, {
                path: "/damrecord",
                component: DaM_record,
                name: 'DaM_record',
                meta: [
                    { name: '' },
                    { name: '' },
                    { name: '' }
                ]
            }, {
                path: "/damlog",
                component: DaM_log,
                name: 'DaM_log',
                meta: [
                    { name: '' },
                    { name: '' },
                    { name: '' }
                ]
            }, {
                path: "/dafdatafrom",
                component: DaF_datafrom,
                name: 'DaF_datafrom',
                meta: [
                    { name: '' },
                    { name: '' }
                ]
            }, {
                path: "/sysconfig",
                component: Sys_sysconfig,
                name: 'Sys_sysconfig',
                meta: [
                    { name: '' },
                    { name: '' }
                ]
            }]
        }
    ]
})

router.beforeEach((to, from, next) => {
    next()
})

export default router